.stats {
  color: white;
  font-family: 'VT323', monospace;
  font-size: 18px;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto 40px;
  margin: auto;
  width: 400px;
}

.stats-content {
  text-align: left;
}

.done-shopping {
  font-size: 40px;
  grid-column-start: 1;
  grid-column-end: span 2;
  text-align: center;
}

.done-shopping:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}

.shop-selection:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}



